* {
  color: #fff;
  margin: 0;
  padding: 0;
  font-family: "montserrat", sans-serif;
  box-sizing: border-box;
}

body {
  background-image: linear-gradient(
    -45deg,
    #001d3d,
    #002650,
    #003064,
    #003a78,
    #00448d,
    #094e9d,
    #1259ae,
    #1964bf,
    #2673cf,
    #3182df,
    #3c92ef,
    #47a2ff
  );
  animation: gradient 20s ease infinite;
  background-size: 400% 400%;
  min-height: 100vh;
}
@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

header {
  font-size: 2rem;
}

header,
form {
  min-height: 18vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

h1 {
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048), 0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
  min-height: 12.5rem;
  min-width: 40vw;
  text-align: center;
  margin: 5rem 0 3rem 0;
  padding: 3.5rem 1rem 0 1rem;
  background: transparent;
}

form {
  padding: 0.5rem;
}

input,
form button {
  -webkit-appearance: none;
  overflow: hidden;
  padding: 0 1rem 0.5rem 0rem;
  font-size: 2rem;
  border: none;
  border-radius: 0;
  box-shadow: 0 1px 0 0 #fff;
  background: none;
  outline: none;
}

::-webkit-input-placeholder {
  color: #d9d9d9;
}

::-moz-placeholder {
  color: #d9d9d9;
}

::placeholder {
  color: #d9d9d9;
}

form button {
  background: none;
  cursor: pointer;
}

.notes-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

h2 {
  padding: 1rem 0 1rem 0.5rem;
  font-weight: 300;
}
h2:nth-of-type(2) {
  padding: 2rem 0 1rem 0.5rem;
}

.notes-list {
  list-style: none;
  margin: 0 auto;
}

.note {
  max-width: 55rem;
  min-width: 15rem;
  word-break: break-all;
  margin: 0.5rem;
  background: rgba(0, 0, 0, 0.3);
  font-size: 1.5rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  transition: all 1s ease;
}

.note li {
  flex: 1;
}

.trash-btn,
.completed-btn {
  background: #ff6f47;
  color: white;
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 1rem;
}
.completed-btn {
  background: rgb(11, 212, 162);
}
.completed-btn-hidden {
  display: none;
}

.note-item {
  padding: 0rem 0.5rem;
}

.fa-trash,
.fa-check {
  pointer-events: none;
}

.completed-note {
  text-decoration: line-through;
  background: rgba(0, 170, 57, 0.5);
  color: #fff;
}

.fade-in {
  animation: fadeIn ease 1s;
  -webkit-animation: fadeIn ease 1s;
  -moz-animation: fadeIn ease 1s;
  -o-animation: fadeIn ease 1s;
  -ms-animation: fadeIn ease 1s;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-moz-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-o-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-ms-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
